import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import * as util from "../utils/Util";

export function TOC() {

    const [titles, setTitles] = useState(null);
    useEffect(() => {
        (async function f1() {
            let slokas = await util.fetcher.slokas;
            setTitles(Object.values(slokas.title));
        })();
    }, []);

    let linkStyle = {textDecoration: 'none', color: 'palegoldenrod', fontWeight: 200, display: "block"}

    return <>
        {titles && <table align={"center"}
                          style={{textAlign: "left", fontSize: 20, lineHeight: 2}}>
            {titles.map((title, i) =>
                <Link key={i} to={`/${i + 1}`} style={linkStyle}>{i + 1 + ". " + title}</Link>
            )}
            <Link to={`/stats`} style={linkStyle}>
                Statistics
            </Link>
            <span style={{fontSize: "0.9rem", color: 'palegoldenrod'}}>
                created this webapp on 19th Dec 2021, <br/>
                in the memory of my daughter <i>Advaita's</i> 6<sup>th</sup> Birthday
            </span>
        </table>
        }
    </>
}